import React from 'react'
import { useParams } from 'react-router-dom'
import { useEffect } from 'react';
import { useState } from 'react';
import  axios  from 'axios';



export default function TvDetails() {
  let {id} = useParams()
  let [tv,setTv] =useState(null)
 

  useEffect (()=>{
    getTvDetails(id)
  },[])


  async function getTvDetails(tvId){
    let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${tvId}?api_key=f458b3f7a348fa9e358e07b8f05965f8&language=en-US`)
    setTv(data)
  }
  return (
    <>
    {tv != null ?   <div className='row'>
    <div className='col-md-4'>
      <img src={`https://image.tmdb.org/t/p/w500` + tv.poster_path} className='w-100' alt="" />
      </div>
      <div className='col-md-8 py-5'>
        <h3>{tv.name}</h3>
        <p className='text-muted'>{tv.first_air_date}</p>
        
        {tv.genres.map((el)=>   <span className='btn btn-info btn-sm mx-2'>{el.name}</span>)}
        
        <p className='text-muted my-3'>Vote : {tv.vote_average}</p>
        <p className='text-muted'>Vote count :{tv.vote_count}</p>
       
        <p className='text-muted my-3'> {tv.overview}</p>
      </div>
      </div>  :  ""   }
    
    </>
  )
}
