import React from "react";
import { Outlet } from "react-router-dom";
import Navbar from "./../Navbar/Navbar";

export default function MasterLayout({user , LogOut}) {
  return (
    <div>
      <Navbar user={user} LogOut={LogOut} />
      <div className="container">
        <Outlet />
      </div>
    </div>
  );
}
