import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

export default function Home() {
  let [moviesList, setMoviesList] = useState([]);
  let [tvList, setTvList] = useState([]);
  let [peopleList, setPeopleList] = useState([]);

  useEffect(() => {
    getData("movie", setMoviesList);
    getData("tv", setTvList);
    getData("person", setPeopleList);
  }, []);

  async function getData(type, callback) {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/trending/${type}/day?api_key=f458b3f7a348fa9e358e07b8f05965f8`
    );

    callback(data.results.slice(0.1));
  }
  return (
    <>
      <div className="row g-3 align-items-center">
        <div className="col-md-4">
          <div className="brdr w-50"></div>
          <h2 className="my-3">
            Trending <br /> Movie <br /> to watch now
          </h2>
          <div className="brdr "></div>
        </div>
        {moviesList.map((movie, i) => (
          <div key={i} className="col-md-2">
            <Link to={"/moviedetils/" + movie.id}>
              <div className="item">
                <img
                  src={`https://image.tmdb.org/t/p/w500` + movie.poster_path}
                  className="w-100"
                  alt=""
                />
                <h3 className="h6">{movie.title}</h3>
              </div>
            </Link>
          </div>
        ))}
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-md-4">
          <div className="brdr w-50"></div>
          <h2 className="my-3">
            Trending <br /> TV <br /> to watch now
          </h2>
          <div className="brdr "></div>
        </div>
        {tvList.map((tv, i) => (
          <div key={i} className="col-md-2">
           <Link to={"/TvDetails/" + tv.id}>
            <div className="item">
              <img
                src={`https://image.tmdb.org/t/p/w500` + tv.poster_path}
                className="w-100"
                alt=""
              />
              <h3 className="h6">{tv.name}</h3>
            </div>
            </Link>
          </div>
        ))}
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-md-4">
          <div className="brdr w-50"></div>
          <h2 className="my-3">
            Trending <br /> people <br /> to watch now
          </h2>
          <div className="brdr "></div>
        </div>
        {peopleList.map((person, i) => (
          <div key={i} className="col-md-2">
            <div className="item">
              <img
                src={`https://image.tmdb.org/t/p/w500` + person.profile_path}
                className="w-100"
                alt=""
              />
              <h3 className="h6">{person.name}</h3>
            </div>
          </div>
        ))}
      </div>
    </>
  );
}
