import React from "react";
import { Link, useParams } from "react-router-dom";
import { useEffect } from "react";
import { useState } from "react";
import axios from "axios";

export default function Tv() {
  let [tvList, setTv] = useState([]);
  useEffect(() => {
    showTv();
  }, []);
  async function showTv() {
    let { data } = await axios.get(
      "https://api.themoviedb.org/3/tv/popular?api_key=f458b3f7a348fa9e358e07b8f05965f8&language=en-US&page=1"
    );
    console.log(data.results);
    setTv(data.results);
  }
  return (
    <>
    <br />
      <div className="container ">
        <div className="row g-4 ">

          {tvList.map( (element , i)=>   <div className="col-md-3 position-relative" key={i}>
        <Link to={"/TvDetails/" + element.id}>
        <div className="">
          <img src={'https://image.tmdb.org/t/p/w500'+element.poster_path}  alt="" className="w-100" />
          <h6>{element.name}</h6>
          <div className="position-absolute top-0 end-0 bg-info vote p-2">{element.vote_average}</div>
          
        </div>
        </Link>
      </div>  )}








    
          
        </div>
      </div>
    </>
  );
}
