import axios from "axios";
import Joi from "joi";

import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
export default function Register() {
  let [user, setUser] = useState({
    first_name: "",
    last_name: "",
    age: "",
    email: "",
    password: "",
  });
  let [errorApi, setErrorApi] = useState("");
  let [errorList, setErrorList] = useState([]);
  let navgiate = useNavigate();
  function addUser(e) {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);
  }
  async function submitForm(e) {
    e.preventDefault();
    let valid = ValidData();
    console.log(valid);
    if (valid.error == null) {
      let { data } = await axios.post(
        "https://sticky-note-fe.vercel.app/signup",
        user
      );
      console.log(data);
      if (data.message == "success") {
        //login
        navgiate("/login");
      } else {
        // error
        setErrorApi(data.message);
      }
    } else {
      setErrorList(valid.error.details);
    }
  }

  function ValidData() {
    let scheme = Joi.object({
      first_name: Joi.string().required().min(3).max(30).alphanum(),
      last_name: Joi.string().required().min(3).max(30).alphanum(),
      age: Joi.number().required().min(16).max(80),
      email: Joi.string()
        .required()
        .email({ tlds: { allow: ["com", "net"] } }),
      password: Joi.string().required(),
    });
    return scheme.validate(user, { abortEarly: false });
  }
  return (
    <div>
      <h2 className="text-center">Regisertion Form </h2>
      {errorList.length > 0
        ? errorList.map((el) => (
            <div className="alert-danger alert">{el.message}</div>
          ))
        : ""}
      {errorApi == "" ? (
        ""
      ) : (
        <div className="alert-danger alert">{errorApi}</div>
      )}

      <form onSubmit={submitForm}>
        <div className="my-2">
          <label htmlFor="first_name">First_name</label>
          <input
            type="text"
            onChange={addUser}
            className="form-control mt-3 bg-transparent text-white"
            id="first_name"
            name="first_name"
          />
        </div>
        <div className="my-2">
          <label htmlFor="last_name">last_name</label>
          <input
            type="text"
            onChange={addUser}
            className="form-control mt-3 bg-transparent text-white"
            id="last_name"
            name="last_name"
          />
        </div>
        <div className="my-2">
          <label htmlFor="age">age</label>
          <input
            type="number"
            onChange={addUser}
            className="form-control mt-3 bg-transparent text-white"
            id="age"
            name="age"
          />
        </div>
        <div className="my-2">
          <label htmlFor="email">email</label>
          <input
            type="email"
            onChange={addUser}
            className="form-control mt-3 bg-transparent text-white"
            id="email"
            name="email"
          />
        </div>
        <div className="my-2">
          <label htmlFor="password">password</label>
          <input
            type="password"
            onChange={addUser}
            className="form-control mt-3 bg-transparent text-white"
            id="password"
            name="password"
          />
        </div>
        <button type="submit" className="btn btn-outline-info">
          Register
        </button>
      </form>
    </div>
  );
}
