import axios from "axios";
import React from "react";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";

export default function Movies() {
  let [moviesList, setMovies] = useState([]);
  let pageNumbers = new Array(10).fill("x").map((el, i) => i + 1);

  useEffect(() => {
    getMovies();
  }, []);
  async function getMovies(pageNum = 1, type = "popular") {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/movie/${type}?api_key=f458b3f7a348fa9e358e07b8f05965f8&language=en-US&page=${pageNum}`
    ); // api

    setMovies(data.results);
  }

  function seeMore(index, overview) {
    console.log(index);
    document.getElementById(index).classList.toggle("d-none");
    //  console.log(overview);
    //  document.getElementById(index).innerHTML = overview.split(' ').slice(10, ).join(' ')
  }
  function changePage(page) {
    
    getMovies(page);
  }
  function changeType(e) {
    let type = e.target.id;
    getMovies(1, type);
  }
  async function search(e) {
    let value = e.target.value;
    console.log(value);
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/search/movie?api_key=f458b3f7a348fa9e358e07b8f05965f8&language=en-US&query=${value}&page=1&include_adult=false`
    );
    setMovies(data.results);
  }
  return (
    <>
      <input
        type="text"
        onChange={search}
        className="form-control bg-transparent text-white w-50 mx-auto my-4"
        placeholder="Search ...."
      />
      <div className="container">
        <div className="row g-4">
          <div className="col-md-2  info">
            <br />
            <p className="side">

            Side bar
            </p>
            
            <hr />
            
            <p id="popular" onClick={changeType}>
              popular
            </p>
            <p id="top_rated" onClick={changeType}>
              top rated
            </p>
            <p id="upcoming" onClick={changeType}>
              upcoming
            </p>
            <p id="now_playing" onClick={changeType}>
              now playing
            </p>
          </div>
          <div className="col-md-10">
            <div className="row"> {" "} {moviesList.length > 0 ? (
                moviesList.map((element, i) => (
                  <div className="col-md-3" key={i}>
                    <Link to={"/moviedetils/" + element.id}>
                    <div className="border-info position-relative">
                      <img src={"https://image.tmdb.org/t/p/w500" + element.poster_path}className="w-100"alt=""/>
                      <br />
                      
                      <br />
                      <h6>{element.title}</h6>
                      
                      <span className="text-muted">
                        {element.overview.split(" ").slice(0, 10).join(" ")}
                      </span>
                      <span className="text-muted d-none" id={i}>
                        {element.overview.split(" ").slice(15).join(" ")}
                      </span>
                      
                      <br />
                      
                     
                      <div className="position-absolute top-0 end-0 bg-info p-2">
                        {element.vote_average.toFixed(1)}
                      </div>
                      
                    </div>
                    </Link>
                    <button
                        onClick={() => seeMore(i, element.overview)}
                        className="btn btn-info btn-sm"
                      >
                        See More
                      </button>
                  </div>
                ))
              ) : (
                <i className="fa-solid fa-spinner-border fa-5x text-info fa-spin my-5 text-center"></i>
              )}
            </div>
          </div>
        </div>
      </div>

      <nav
        aria-label="..."
        className="d-flex text-align-center justify-content-center m-2 p-1 "
      >
        <ul className="pagination pagaengin pagination-sm">
          {pageNumbers.map((el, i) => (
            <li key={i} className="page-item" onClick={() => changePage(el)}>
              <a className="page-link">{el}</a>
            </li>
          ))}
        </ul>
      </nav>
    </>
  );
}
