import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import axios from "axios";

export default function People() {
  let [peopleList, setPerson] = useState([]);

  useEffect(() => {
    getPeople();
  }, []);

  async function getPeople() {
    let { data } = await axios.get(
      "https://api.themoviedb.org/3/person/popular?api_key=f458b3f7a348fa9e358e07b8f05965f8&language=en-US&page=1"
    );
    console.log(data.results);
    setPerson(data.results);
  }

  return (
    <>
      {/* <div className="container">
        <div className="row g-4">
          {peopleList.map((person, i) => (
            <div key={i} className="col-md-3">
              <h6>{person.original_title}</h6>
              <p>{person.overview}</p>
              <img src={"https://image.tmdb.org/t/p/w500" + person.backdrop_path} alt="" />
            </div>
          ))}
        </div>
      </div> */}


      <div className="container">
        <div className="row">
          {peopleList.map(  (person , i)=>   <div key={i} className="col-md-2">
          <div className="item">
              <img
                src={`https://image.tmdb.org/t/p/w500` + person.profile_path}
                className="w-100"
                alt=""
              />
              <h3 className="h6">{person.name}</h3>
            </div>
          </div>  )}
        
        </div>
      </div>
    </>
  );
}
