import React from 'react'
import { useParams } from 'react-router-dom'
import { useEffect } from 'react';
import { useState } from 'react';
import  axios  from 'axios';

export default function MovieDetiles() {
  let {id} = useParams()
  let [movie,setMovie] =useState(null)


  useEffect (()=>{
    getMovieDetails(id)
  },[])


  async function getMovieDetails(movieId){
    let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}?api_key=f458b3f7a348fa9e358e07b8f05965f8&language=en-US`)
    setMovie(data)
  }
  return (
    <>
    {movie != null ?   <div className='row'>
      <div className='col-md-4'>
      <img src={`https://image.tmdb.org/t/p/w500` + movie.poster_path} className='w-100' alt="" />
      </div>
      <div className='col-md-8 py-5'>
        <h3>{movie.title}</h3>
        <p className='text-muted'>{movie.tagline}</p>
        
        {movie.genres.map((el)=>   <span className='btn btn-info btn-sm mx-2'>{el.name}</span>)}
        
        <p className='text-muted my-3'>Vote : {movie.vote_average}</p>
        <p className='text-muted'>Vote count :{movie.vote_count}</p>
        <p className='text-muted'> budget : {movie.budget}</p>
        <p className='text-muted my-3'> {movie.overview}</p>
      </div>
      </div>  :  ""   }
    
    </>
  )
}
